<template>
    <div class="choose-wrap">

        <div class="choose-inner">

            <div>
                <h3>請選擇版型</h3>

                <fieldset>
                    <label for="picked_x">寬邊格數</label>
                    <input type="number" min="1" id="picked_x" v-model="picked_x">
                </fieldset>

                <fieldset>
                    <label for="picked_y">高邊格數</label>
                    <input type="number" min="1" id="picked_y" v-model="picked_y">
                </fieldset>
            </div>

            <div>
                <h3>請選擇比例</h3>
                <fieldset>
                    <input type="radio" id="1/1" value="1/1" v-model="picked_ratio" />
                    <label for="1/1">1/1</label>
                    <div class="box" style="aspect-ratio: 1/1;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="5/4" value="5/4" v-model="picked_ratio" />
                    <label for="5/4">5/4</label>
                    <div class="box" style="aspect-ratio: 5/4;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="4/3" value="4/3" v-model="picked_ratio" />
                    <label for="4/3">4/3</label>
                    <div class="box" style="aspect-ratio: 4/3;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="3/2" value="3/2" v-model="picked_ratio" />
                    <label for="3/2">3/2</label>
                    <div class="box" style="aspect-ratio: 3/2;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="16/10" value="16/10" v-model="picked_ratio" />
                    <label for="16/10">16/10</label>
                    <div class="box" style="aspect-ratio: 16/10;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="16/9" value="16/9" v-model="picked_ratio" />
                    <label for="16/9">16/9</label>
                    <div class="box" style="aspect-ratio: 16/9;"></div>
                </fieldset>

                <fieldset>
                    <input type="radio" id="2/1" value="2/1" v-model="picked_ratio" />
                    <label for="2/1">2/1</label>
                    <div class="box" style="aspect-ratio: 2/1;"></div>
                </fieldset>
            </div>
            
            <div>
                <button @click="chooseTemplate">確定</button>
            </div>

            


            
        </div>
        
    </div>
</template>

<script>
import { ref } from 'vue';

export default {
    name: 'SixteenGriderChooseTemplate',
    emits: [
        'eventChooseTemplate',
    ],

    setup(props, context) {
        // 數據
        let picked_x = ref(4);
        let picked_y = ref(4);
        let picked_ratio = ref('1/1');

        // 方法
        function chooseTemplate() {
            context.emit('eventChooseTemplate', `${picked_x.value}x${picked_y.value}`, picked_ratio.value)
        }



        return {
            picked_x,
            picked_y,
            picked_ratio,

            chooseTemplate,
        }
    }
}
</script>