<template>

    <section class="work-wrap" 
        :style="{
            'aspect-ratio': initData.ratio
        }">

        <!-- 另存為圖片的區塊 -->
        <div ref="captureRef" class="capture-wrap">

            <!-- 文字 -->
            <div class="year-wrap" v-show="initData.year_display">
                <span ref="yearRef" :style="{
                    'color': initData.year_color,
                    'font-size': initData.year_font_size + 'px',
                    'font-weight': initData.year_font_weight,
                    'font-family': initData.year_font_family_name,
                    'transform': 'scale(1)'
                }">{{ initData.year_text }}</span>
            </div>
            
            <!-- 格子容器 -->
            <div class="grid-wrap" 
                :style="{
                    'grid-template-columns': `repeat(${initData.count_x}, 1fr)`,
                    'grid-template-rows': `repeat(${initData.count_y}, 1fr)`,
                    'gap': `${initData.grid_gap}px`,
                    'background-color': initData.background_color,
                }">

                <!-- 圖片容器 -->
                <div v-for="(item, i) in imgInfo" :key="i" class="image-wrap">
                    <!-- 上傳圖片按鈕 -->
                    <input type="file" class="edit-btn" 
                        @change="setImage($event, i)" 
                        :key="item.fileInputKey">

                    <!-- 重設圖片按鈕 -->
                    <button class="reset-btn"
                        v-show="item.imgSrc" 
                        @click="resetImage(i)" >重設圖片</button>

                    <!-- 其他設定欄位 -->
                    <div class="control-wrap">
                        <!-- 是否設定 cover 尺寸 -->
                        <fieldset v-show="item.imgSrc">
                            <input :id="`img-type-${i}`" type="checkbox" v-model="item.coverSize">
                            <label :for="`img-type-${i}`" >cover尺寸</label>
                        </fieldset>

                        <!-- 背景顏色 -->
                        <fieldset>
                            <label :for="`img-type-${i}`">背景色</label>
                            <input type="color" 
                                :name="`img-bg-color-${i}`" 
                                :id="`img-bg-color-${i}`" 
                                v-model="item.bgColor">
                        </fieldset>
                    </div>

                    
                    
                    <!-- 背景顏色 -->
                    <div class="bg-color" :style="{
                        'background-color': item.bgColor,
                    }"></div>

                    <!-- 上傳的圖片 -->
                    <!-- <img v-show="imgSrcList[i]" :src="imgSrcList[i]" alt="your image" /> -->
                    <div class="bg-img" 
                        v-show="item.imgSrc"
                        :style="{
                            'background-image': `url(${item.imgSrc})`,
                            'background-size': item.coverSize ? 'cover' : 'contain'
                        }"></div>
                </div>

            </div>
        </div> 
    </section>

</template>

<script>
import { ref, reactive, toRef } from 'vue';


export default {
    name: 'SixteenGriderBody',
    props: ['data'],

    setup(props) {
        // 從 props 來的數據
        const initData = toRef(props, 'data');

        // 數據

        // 圖片資訊陣列
        let imgInfo = reactive([]);
        for ( let i=0 ; i < initData.value.count_x * initData.value.count_y ; i++ ) {
            imgInfo.push({
                fileInputKey: `${i}-0`,  // file input 的 key 值
                imgSrc: '',  // 上傳圖片的路徑
                coverSize: true,  // 是否設定為 cover 尺寸
                bgColor: 'transparent',  // 背景顏色
            })
        }


        // 元素
        let captureRef = ref(null);
        let yearRef = ref(null);


        // 方法

        // 設定圖片
        function setImage(event, index) {
            /* refrence: http://jsfiddle.net/LvsYc/ */
            if (event.target.files && event.target.files[0]) {
                var reader = new FileReader();
                
                reader.onload = function(event) {
                    // 更新畫面上的圖片 src
                    imgInfo[index].imgSrc = event.target.result;
                }
                reader.readAsDataURL(event.target.files[0]); // convert to base64 string
            }
        }

        // 重設圖片
        function resetImage(index) {
            // 重設畫面上的圖片 src
            imgInfo[index].imgSrc = '';

            // 重設 file input 的值
            // How to reset/clear file Input
            // https://stackoverflow.com/questions/59658463/how-to-reset-clear-file-input
            let tempArr = imgInfo[index].fileInputKey.split('-');  // 分割 key 字串 "索引-流水號"
            let newIndex = parseInt(tempArr[1]) + 1;  // 累加流水號
            imgInfo[index].fileInputKey = `${tempArr[0]}-${newIndex}`;  // 更新 key 值
        }


        return {
            initData,

            // imgSrcList,
            // fileInputKey,
            imgInfo,

            captureRef,
            yearRef,

            

            setImage,
            resetImage,
        }
    }
}
</script>